.pieceBackground {
  background-color: blue;
}
.piece {
  width: 3.5vmax;
  height: 3.5vmax;
  border-radius: 50%;
  margin: 0.5vmax;
}
.piece-empty {
  border: 2px solid white;
  background-color: black;
}

.piece-player {
  border: 2px solid white;
  background-color: red;
}

.piece-AI {
  border: 2px solid white;
  background-color: gold;
}

.piece-selector {
  width: 3.5vmax;
  height: 3.5vmax;
}

.piece-selector:hover {
  background-color: red;
  border: 2px solid white;
  cursor: pointer;
}

.winningPiece {
  border: 4px solid #00ff37 !important;
}
